#toTop {
  position: fixed;
  display: inline-block;
  width: 120px;

  top: 80%;
  left: 70%;
  z-index: 0;
  cursor: pointer;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
  @media screen and(min-width: $tablet) {
    left: 87%;
  }
  @media screen and(min-width: $desktop) {
    left: 92%;
  }
  @media screen and(min-width: 2000px) {
    width: 150px;
  }
  @media screen and(min-width: 4000px) {
    width: 250px;
  }
}
// #toTop:hover {

// }

#toTop svg {
  position: relative;
  top: 12px;
  right: -1px;
  fill: #fff;
  transition: all 0.3s ease-in-out;
}

#toTop:hover svg {
  cursor: pointer;
  fill: #fff;
  transition: all 0.3s ease-in-out;
}

#toTop.show {
  opacity: 1;
  visibility: visible;
  right: calc(8%);
  bottom: calc(11%);
}

.to-top-arrow {
  transform: rotate(180deg);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    transform: rotate(0deg);
  }
}
